.App {
  text-align: center;
  height: calc(100% - 5vh - 64px);
  position: fixed;
  top: 64px;
  width: 100vw;
}

header.MuiAppBar-root {
  height: 64px;
}

.App-logo {
  height: 8vmin;
  pointer-events: none;
  margin-bottom: 2vh;
}

.App-logo-holder {
  max-height: 8vmin;
}

.App-header {
  height: 100%;
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header.portfolio-page {
  justify-content: start;
}

footer {
  height: 5vh;
  background-color: indigo;
  font-size: 3vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2vmin;
  position: fixed;
  bottom: 0;
  width: 100vw;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

label.MuiFormControlLabel-root.Mui-disabled .MuiTypography-root {
  color: grey;
}

label.MuiFormControlLabel-root .MuiTypography-root {
  font-size: 2vmin;
  font-weight: bolder;
}

.Mui-disabled span.MuiSwitch-track {
  background-color: grey;
}

.selected-button {
  color: red;
}

.MuiButtonBase-root.Mui-disabled .MuiSvgIcon-root {
  background: gray;
}